/**
 * Users API Service
 * Handles all user-related API calls
 */

import { apiClient } from './client'
import type { UsersResponse, GetUsersParams } from '../types/users'

class UsersService {
  /**
   * Get all users with pagination
   */
  async getAllUsers(params?: GetUsersParams): Promise<{ success: boolean; data?: UsersResponse; error?: string; message?: string }> {
    try {
      // Build input with defaults as per API requirements
      const input: Record<string, any> = {
        membershipStatus: params?.membershipStatus || "all", // Default to "all"
        page: params?.page || 1, // Default to 1
        limit: params?.limit || 20, // Default to 20
      }
      
      // Add search if provided
      if (params?.search) {
        input.search = params.search
      }

      console.log('[Users Service] Fetching users with params:', input)
      
      // tRPC POST format: { "0": { "json": input } }
      const tRPCBody = { "0": { "json": input } }
      
      console.log('[Users Service] POST Body:', JSON.stringify(tRPCBody, null, 2))
      
      const response = await apiClient.post<UsersResponse>('/api/trpc/admin.users.getAll', tRPCBody)
      
      console.log('[Users Service] API Response:', response)

      if (response.success && response.data) {
        return {
          success: true,
          data: response.data,
          message: 'Users fetched successfully',
        }
      }

      const errorMsg = typeof response.error === 'string' 
        ? response.error 
        : typeof response.error === 'object' && (response.error as any)?.message
        ? (response.error as any).message
        : typeof response.message === 'string'
        ? response.message
        : 'Failed to fetch users'

      return {
        success: false,
        error: errorMsg,
        message: errorMsg,
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'An unexpected error occurred'
      console.error('[Users Service] Error:', error)
      return {
        success: false,
        error: errorMessage,
        message: errorMessage,
      }
    }
  }

  /**
   * Get user by ID
   */
  async getUserById(userId: string): Promise<{ success: boolean; data?: any; error?: string }> {
    try {
      const response = await apiClient.get(`/api/trpc/admin.users.getById?input=${encodeURIComponent(JSON.stringify({ id: userId }))}`)
      
      if (response.success && response.data) {
        return {
          success: true,
          data: response.data,
        }
      }

      return {
        success: false,
        error: response.error || 'Failed to fetch user',
      }
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : 'An unexpected error occurred',
      }
    }
  }
}

// Export singleton instance
export const usersService = new UsersService()

